@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap');

.home{
    flex-grow: 1;
    padding-top: 100px;
    padding-bottom: 100px;
}
.home__container {
    column-gap: 50px;
    align-items: center;
    justify-content: center;
}
.home__text {
    font-family: "Poppins", sans-serif;
    font-weight: 700;
    font-size: 58px;
    line-height: 70px;
    color: #42446e;
    letter-spacing: -1px;
    flex-grow: 1;
}
.home__text span {
    background: linear-gradient(90deg, #13b0f5 -2.06%, #e70faa 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
}
.home__emodji {
    max-width: 10%;
}
.border{
    width: 367px;
    height: 367px;
    background: linear-gradient(90deg, #13b0f5 -2.06%, #e70faa 100%);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.home__user-photo {
    border-radius: 50%;
    max-width: 349px;
}
