@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");

.about {
    flex-grow: 1;
}
.about__container {
    font-family: "Poppins", sans-serif;
    flex-direction: column;
}
.about__block {
    display: flex;
    flex-direction: column;
    max-width: 704px;
}
.about__block:not(:last-child) {
    margin-bottom: 38px;
}
.about__title {
    font-weight: 700;
    font-size: 42px;
    line-height: 52px;
    letter-spacing: -0.4px;
    color: #42446e;
    margin-bottom: 38px;
}
.about__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 26px;
    color: #666666;
}
.about__items {
    display: flex;
    flex-direction: column;
    row-gap: 30px;
}
.about__item {
    display: flex;
    flex-direction: column;
    row-gap: 4px;
}
.about__level {
    display: flex;
}
.about__level div {
    display: flex;
    column-gap: 7px;
    align-items: center;
}
.about__profession {
    flex-grow: 1;
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    letter-spacing: 1px;
    color: #666666;
}
.about__button {
    flex: 0 1 84px;
    min-height: 24px;
    background: #d7ffe0;
    border-radius: 100px;
    font-weight: 600;
    font-size: 9px;
    line-height: 26px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #018c0f;
}
.about__data {
    font-weight: 500;
    font-size: 12px;
    line-height: 28px;
    letter-spacing: 1px;
    color: #a7a7a7;
}
.about__line {
    height: 2px;
    background: #ebeaed;
    margin-top: 20px;
}
.about__level div:nth-child(1) {
    flex-grow: 1;
}
.about__level ~ .about__level {
    column-gap: 50px;
}
