@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;700&display=swap");

.projects {
    flex-grow: 1;
}
.projects__container {
    flex-direction: column;
    font-family: "Poppins", sans-serif;
}
.projects__headlines {
    display: flex;
    flex-direction: column;
    row-gap: 32px;
    margin-bottom: 111px;
}
.projects__title {
    font-weight: 700;
    font-size: 42px;
    line-height: 26px;
    color: #42446e;
}
.projects__text {
    font-weight: 400;
    font-size: 20px;
    line-height: 26px;
    color: #666666;
}
/* ````````````````````````````````````````````````````````` */
.projects__cards {
    display: flex;
    flex-wrap: wrap;
    row-gap: 62px;
    column-gap: 46px;
    justify-content: space-between;
}
.card {
    display: flex;
    flex-direction: column;
    flex: 0 1 373px;
    height: 100%;
    box-shadow: 2px 2px 100px rgba(0, 0, 0, 0.2);
    border-radius: 20px;
    max-width: 373px;
}
/* ````````````````````````````````````````````````````````` */
.card__link-image {
    margin-bottom: 27px;
}
.card__body {
    padding: 0 29.5px;
    margin-bottom: 25px;
}
.card__content {
    display: flex;
    flex-direction: column;
    margin-bottom: 24px;
}
.card__title {
    font-weight: 500;
    font-size: 28px;
    line-height: 26px;
    color: #000000;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 17px;
}
.card__text {
    font-weight: 300;
    font-size: 18px;
    line-height: 26px;
    color: #666666;
    margin-bottom: 12px;
}
.card__specification {
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: #42446e;
}
.card__links {
    display: flex;
    text-decoration-line: underline;
    justify-content: space-between;
    column-gap: 15px;
}
.card__links a {
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: #000000;
}
.card__link {
    display: flex;
    column-gap: 13px;
    align-items: center;
}
