*, *::before, *::after {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	border: 0;
}
li {
	list-style: none;
}
a, a:visited {
	text-decoration: none;
}
a:hover {
	text-decoration: none;
}
h1, h2, h3, h4, h5, h6 {
	font-weight: inherit;
	font-size: inherit;
}
img {
	vertical-align: top;
}

body, html {
	height: 100%;
}

#root {
	min-height: 100vh;
}

.wrapper{
	min-height: 100vh;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	/* background-color: #191919; */
}

[class*="__container"]{
	display: flex;
	margin: 0 auto;
	/* max-width: 1223px; */
	max-width: 1248px;
	padding-left: 15px;
	padding-right: 15px;
	/* justify-content: center; */
}

