@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap");

.skills {
    flex-grow: 1;
}
.skills__container {
    flex-direction: column;
}
.skills__headlines {
    margin-bottom: 205px;
    font-family: "Poppins";
    line-height: 26px;
}
.skills__headlines h2 {
    font-weight: 700;
    font-size: 42px;
    display: flex;
    align-items: center;
    color: #42446e;
    margin-bottom: 31px;
}
.skills__headlines p {
    font-weight: 400;
    font-size: 20px;
    color: #666666;
}
.skills__list {
    display: flex;
    flex-direction: column;
    row-gap: 69px;
}
.skills__list-top,
.skills__list-bottom {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    column-gap: 20px;
}
.skills__list-top li,
.skills__list-bottom li {
    width: 105px;
}
