@import url('https://fonts.googleapis.com/css2?family=DM+Sans&display=swap');


.footer{
    background-color: #191919;
    margin-top: 100px;
}
.footer__container{
    flex-direction: column;
    padding-top: 41px;
    padding-bottom: 60px;
}
.footer__top{
    display: flex;
    column-gap: 48px;
    margin-bottom: 45px;
    align-items: center;
}
.footer__logo{
    flex-grow: 1;
}
.footer__contacts{
    font-family: 'DM Sans', sans-serif;
    color: #A7A7A7;
    display: flex;
    font-size: 18px;
    font-weight: 400;
    line-height: 26px;
    column-gap: 25px;
}
.footer__line{
    background-color: #42446E;
    height: 2px;
    opacity: 0.3;
    margin-bottom: 60px;
}

