@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap");

.project {
    flex-grow: 1;
}
.project__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: "Poppins", sans-serif;
}
.project__title {
    font-weight: 700;
    font-size: 42px;
    line-height: 26px;
    color: #7175bb;
    margin-bottom: 100px;
}
.project__card {
    background-color: rgb(223, 223, 223);
    padding: 55px;
    border-radius: 25px;
    box-shadow: 0px 0px 100px rgba(0, 0, 0, 0.5);
    margin-bottom: 70px;
}
.project__image {
    width: 100%;
    height: 100%;
    border-radius: 20px;
}
.project__skills {
    font-size: 25px;
    font-weight: 700;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 50px;
}
.project__link {
    min-width: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 20px;
    padding: 15px 20px;
    border: 2px solid black;
    border-radius: 5px;
}
.project__image {
    flex: 0 1 50px;
}
.project__text {
    color: black;
    font-weight: 700;
    font-size: 18px;
}
